import React, { useCallback, useRef, useState } from 'react'
import cx from 'classnames'
import { useHistory } from "react-router-dom";

import {Toast} from 'antd-mobile'
import './index.css'

const Home = () => {
  const [name, setName] = useState('')
  const [passward, setPassward] = useState('')
  const initPassward = useRef<number>(0)
  const history = useHistory()
  const clickHandle = useCallback(() => {
    initPassward.current += 1
    if (!name || !passward) {
      Toast.show('准确输入嘛~😡')
      return
    }
    if (name !== 'qingqingxiannv') {
      Toast.show('你难道忘记自己是一个小🧚‍♀️了嘛')
      return
    }
    if (passward !== '0903') {
      let text = ''
      if (initPassward.current <= 3) {
        text = '密码错了噢~'
      } else {
        text = '相识的日期～😊'
      }
      Toast.show(text)
      return
    }
    console.log('correct')
    history.push('/subPage')
  }, [history, name, passward])
  return (
    <div className='home__wrap'>
      <h2 className='home__wrap-title animate__animated animate__lightSpeedInRight'>清清🧚‍♀️生日快乐</h2>
      <h4 className='home__wrap-subTitle animate__animated animate__lightSpeedInLeft animate__delay-1s'>这里有一份礼物请查收噢～😊～</h4>
      <p className='home__wrap-desc animate__animated animate__fadeInDownBig animate__delay-2s'>
        账户名称是:<b>qingqingxiannv</b>
        <br />
        至于密码就看你的啦（四位数字噢）
      </p>
      <section className='home__wrap-content animate__animated animate__fadeIn animate__delay-3s'>
        <section className='inputwrapper'>
          <input type="text" className={'input'} onChange={(e: any) => setName(e.target.value)} />
          <input
            type="passward"
            className={cx('input','passward')}
            onChange={(e: any) => setPassward(e.target.value)}
          />
        </section>
      </section>
      <div className="btn-wrapper ">
        <div className='btn animate__animated animate__rotateIn animate__delay-4s' onClick={clickHandle}>
          <span>Click me!(๑•̀ㅂ•́)و✧</span>
        </div>
      </div>
    </div>
  )
}

export default React.memo(Home)
