import React, { useRef } from 'react'
import './index.css'
import starSky from '../../statics/voice/starsky.mp3'
import { useMounted } from '../../hooks'
import { useHistory } from 'react-router'
const SubPage = () => {
  const audioRef = useRef<HTMLAudioElement>(null)
  const history = useHistory()

  useMounted(() => {
    audioRef.current?.play()
  })
  const clickHandle = () => {
    history.push('/photo')

  }
  return (
    <div className={'subpage__wrap'}>
      <audio autoPlay={true} ref={audioRef} loop={true} preload="auto" src={starSky}></audio>
      <div className={'subpage__wrap-content'}>
        <h1 className={'content-title1'}>一辈子很短，如白驹过隙，转瞬即逝</h1>
        <h2 className={'content-title2'}>可这种心情很长，如高山大川，延绵不绝</h2>
        <span className={'content-title3'}>27岁，祝你生日快乐，开心最重要😊</span>
      </div>
      <div onClick={clickHandle} className={'subpage__wrap-btn'}>go forward φ(≧ω≦*)♪</div>

      <h2 className={'fixedtitle'}>——最美的遇见</h2>
    </div>
  )
}
export default React.memo(SubPage)
